import React from "react"

import Layout from "../components/layout"
import SEO from "../components/seo"
import { Grid } from "@horacioh/gatsby-theme-instagram"


const GalleryPage = () => (
  <Layout>
    <SEO title="Gallery" />
    <div className="gallry__wrapper">
      <Grid />
  </div>
  </Layout>
)

export default GalleryPage
